import Table from 'react-bootstrap/Table'
import { useDispatch, useSelector } from 'react-redux'
import { memo, useState } from 'react'
import { prdtDecrease, prdtIncrease, deleteItem } from "./../store.js"


function Cart() {
    // a  state  , user state  return state.user
    const cartData = useSelector((state)=>{ return state.cart }) // redux store  
    const dispatch = useDispatch()  // useDispatch store.js   
    const user = useSelector((state) => {return state})
    const [count, setCount] = useState(0)

    return (
        <>
            <Table>
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {
                        cartData.map(function(a, i) {
                            return (
                                <tr className="data-list" key = {i}>
                                    <td>{cartData[i].id}</td>
                                    <td>{cartData[i].name}</td>
                                    <td className="btn-count">
                                        <button onClick={
                                            () => {
                                                dispatch(prdtDecrease(cartData[i].id))
                                            }
                                        }>-</button>{cartData[i].count}
                                        <button onClick = {
                                            () => {
                                                //      id 
                                                dispatch(prdtIncrease(cartData[i].id))
                                            }
                                        }>+</button>
                                    </td>
                                    <td>
                                        <button onClick={() => {
                                           dispatch(deleteItem(cartData[i].id))
                                        }}>
                                            <img className="btn-delete" src={require('../img/btn_delete.png')}></img>
                                        </button>
                                    </td>
                                </tr>
                            )
                        })
                    }
                </tbody>
            </Table>
            <button></button>
        </>
        
    )
}

export default Cart