import { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { useDispatch, useSelector } from 'react-redux'
import SwiperBg from '../routes/SwiperBg'
import FastMenu from './FastMenu'
import axios from 'axios'
import { getNewData } from '../store'

function Main() {
    //   ,    state
    let [communication, setCommunication] = useState(false)
   
    const dispatch = useDispatch()

    //  
   // setCommunication(true)
    useEffect(() => {
        axios.get('https://z1z1hh.github.io/shop/data.json')
            .then((response) => { 
                console.log('')
        
                console.log(communication);
                setCommunication(true);
     
                //  
                //   
                const getData = response.data;
                console.log(getData);
                dispatch(getNewData(getData));

                //    
                setCommunication(false);
            })
            .catch((error) => {
                //  
                console.log(error);
                setCommunication(false);
            })  
    }, [])


    return (
        <>  
            <SwiperBg></SwiperBg>
            <FastMenu></FastMenu>
        </>
    )
}

function NoData() {
    return (
        <div>
            <p>   .</p>
        </div>
    )
}

function LoadingBar() {
    return (
        <div className="loading-wrap">
            <div className="loading-bar">
                <img src="/Spin-loading.gif" />
            </div>
        </div>
    )
    
}

export default Main;