import { createContext, useState } from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import data from './data.js'
import Main from './routes/Main'
import { Routes , Route , useNavigate } from 'react-router-dom'
import Detail from './routes/Detail.js'
import Cart from './routes/Cart'
import { useSelector } from 'react-redux'
import axios from 'axios';
import { useQuery } from 'react-query';

export let Context1 = createContext() //  = state 

function App() {
  //  
  let [shoes, setShoes] = useState(data)
  let [stock] = useState([10,11,12])
  const cartData = useSelector((state)=>{ return state.cart }) // redux store  
  const [cartDataLength] = useState(cartData.length)

  //  
  let navigate = useNavigate()

  const result = useQuery(['userName'], () => 
    axios.get('https://codingapple1.github.io/userdata.json')
    .then((a) =>{
        return a.data;
    }),
    { staleTime : 2000 }
  );

  return (
    <div className="App">
      <div className="navbar-wrap">
        <div className="main-logo" onClick={()=>{
          navigate('/')
        }}></div>
        <div className="submenu-wrap">
         
          <li>
            
          </li>
          <li>
            <span></span>
          </li>
          <li>
            <span></span>
            <span onClick={()=>{
               navigate('/cart')
            }}>
              {
                cartDataLength >= 1 ? <span className="bag-count">{cartDataLength}</span> : null
              }
            </span>
             
          </li>
          <li>
            <span>{ result.isLoading ? ' .' : result.data.name + ''}</span>
          </li>
        </div>
      </div>

      <div className="subnavbar-wrap">
        <button onClick={() => {
          navigate('/menu')
        }}></button>
        <button>e-</button>
        <button> </button>
        <button>, </button>
        <button></button>
        <button> </button>
      </div>
      
      <Routes>
        <Route path="/" element={ <Main />} />
        <Route path="/detail/:id" element={ 
          <Context1.Provider value={{ stock }}>
             <Detail shoes={shoes} />
          </Context1.Provider>
        } />
        <Route path="*" element={<div> </div> } />
        
        <Route path="/menu" ></Route>
        <Route path="/cart" element={<Cart />} />
      </Routes>
      
    </div>
  );
}


export default App;
